html, body, #root {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
}

.jumbotron {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 300px;
  background-color: #6f5499;
  color: #cdbfe3;
  
  font-size: 32px;
}

.github-link {
  margin-top: 20px;
}

.menu {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: stretch;
      align-items: stretch;
  -ms-flex-pack: center;
      justify-content: center;
  color: #563d7c;
  border-bottom: 1px solid #f0f0f0;
}

.menu-item {
  font-size: 18px;
  font-variant: small-caps;
  padding: 18px;
  cursor: pointer;
}

.menu-item:hover,
.menu-item--active {
  background-color: #f9f9f9;
  text-decoration: underline;
}




